/**
 * ‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏• Film Data Contacts ‡∏û‡∏£‡πâ‡∏≠‡∏°‡∏à‡∏≥‡∏ô‡∏ß‡∏ô consult ‡πÅ‡∏•‡∏∞‡∏ô‡∏±‡∏î‡∏ú‡πà‡∏≤‡∏ï‡∏±‡∏î
 * @param date - ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà‡πÉ‡∏ô‡∏£‡∏π‡∏õ‡πÅ‡∏ö‡∏ö YYYY-MM-DD (optional)
 * @param count - ‡∏Ç‡∏≠ count summary ‡∏î‡πâ‡∏ß‡∏¢‡πÑ‡∏´‡∏° (default: true)
 * @param today - ‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏ß‡∏±‡∏ô‡∏ô‡∏µ‡πâ‡πÄ‡∏ó‡πà‡∏≤‡∏ô‡∏±‡πâ‡∏ô (default: false)
 */
export async function fetchFilmDataContacts(
  date?: string,
  count: boolean = true,
  today: boolean = false
): Promise<FilmDataContactsResponse> {
  try {
    const params = new URLSearchParams();
    if (date) params.append("date", date);
    if (count) params.append("count", "true");
    if (today) params.append("today", "true");
    const url = `${PYTHON_API_URL}/api/film-data-contacts?${params.toString()}`;
    const response = await fetch(url, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    });
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    const data = await response.json();
    return data;
  } catch (error) {
    console.error("Error fetching film data contacts:", error);
    throw error;
  }
}
/**
 * ‡πÅ‡∏õ‡∏•‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏• Film Data ‡πÄ‡∏õ‡πá‡∏ô‡∏à‡∏≥‡∏ô‡∏ß‡∏ô‡πÅ‡∏¢‡∏Å‡∏ï‡∏≤‡∏° Agent
 * ‡∏£‡∏≠‡∏á‡∏£‡∏±‡∏ö‡∏ó‡∏±‡πâ‡∏á field ‡∏†‡∏≤‡∏©‡∏≤‡πÑ‡∏ó‡∏¢‡πÅ‡∏•‡∏∞‡∏≠‡∏±‡∏á‡∏Å‡∏§‡∏©
 * ‡∏™‡∏°‡∏°‡∏ï‡∏¥‡∏ß‡πà‡∏≤ contact_person ‡∏°‡∏µ‡∏£‡∏π‡∏õ‡πÅ‡∏ö‡∏ö‡πÄ‡∏ä‡πà‡∏ô "‡∏™‡∏≤", "‡∏û‡∏±‡∏î‡∏ä‡∏≤", "‡∏ï‡∏±‡πâ‡∏á‡πÇ‡∏≠‡πã" (‡∏ä‡∏∑‡πà‡∏≠‡πÄ‡∏ã‡∏•‡∏•‡πå)
 */
export function transformFilmDataToAgentCounts(
  data: FilmDataContactsResponse
): FilmDataCountsByAgent {
  const consultCounts: Record<string, number> = {};
  const surgeryCounts: Record<string, number> = {};
  // Map ‡∏ä‡∏∑‡πà‡∏≠‡πÄ‡∏ã‡∏•‡∏•‡πå‡πÑ‡∏õ‡πÄ‡∏õ‡πá‡∏ô Agent ID
  const agentNameMap: Record<string, string> = {
    ‡∏™‡∏≤: "101",
    ‡∏û‡∏±‡∏î‡∏ä‡∏≤: "102",
    ‡∏ï‡∏±‡πâ‡∏á‡πÇ‡∏≠‡πã: "103",
    Test: "104",
    ‡πÇ‡∏≠‡πã: "105",
    ‡πÄ‡∏ï‡πâ: "106",
    ‡πÄ‡∏≠‡πâ: "107",
    ‡∏≠‡πâ‡∏≠‡∏°: "108",
  };
  // ‡∏ô‡∏±‡∏ö‡∏à‡∏≤‡∏Å‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡πÅ‡∏ï‡πà‡∏•‡∏∞‡πÅ‡∏ñ‡∏ß
  data.data.forEach((row) => {
    // ‡∏£‡∏≠‡∏á‡∏£‡∏±‡∏ö‡∏ó‡∏±‡πâ‡∏á field ‡∏†‡∏≤‡∏©‡∏≤‡∏≠‡∏±‡∏á‡∏Å‡∏§‡∏©‡πÅ‡∏•‡∏∞‡πÑ‡∏ó‡∏¢
    const contact = row.contact_person || row["‡∏ú‡∏π‡πâ‡∏ï‡∏¥‡∏î‡∏ï‡πà‡∏≠"] || "";
    const consultDate = row.consult_date || row["‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà‡πÑ‡∏î‡πâ‡∏ô‡∏±‡∏î consult"] || "";
    const surgeryDate =
      row.surgery_appointment_date || row["‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà‡πÑ‡∏î‡πâ‡∏ô‡∏±‡∏î‡∏ú‡πà‡∏≤‡∏ï‡∏±‡∏î"] || "";
    // ‡∏î‡∏∂‡∏á Agent ID ‡∏à‡∏≤‡∏Å‡∏ä‡∏∑‡πà‡∏≠‡∏ú‡∏π‡πâ‡∏ï‡∏¥‡∏î‡∏ï‡πà‡∏≠
    const contactName = contact.trim();
    const agentId = agentNameMap[contactName];
    if (!agentId) {
      // ‡∏ñ‡πâ‡∏≤‡πÑ‡∏°‡πà‡πÄ‡∏à‡∏≠‡πÉ‡∏ô map ‡∏•‡∏≠‡∏á‡∏î‡∏∂‡∏á‡∏à‡∏≤‡∏Å‡∏ï‡∏±‡∏ß‡πÄ‡∏•‡∏Ç 3 ‡∏´‡∏•‡∏±‡∏Å
      const agentMatch = contactName.match(/^(\d{3})/);
      if (!agentMatch) return;
      const extractedId = agentMatch[1];
      // ‡∏ô‡∏±‡∏ö consult ‡∏ñ‡πâ‡∏≤‡∏°‡∏µ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà
      if (consultDate && consultDate.trim() !== "") {
        consultCounts[extractedId] = (consultCounts[extractedId] || 0) + 1;
      }
      // ‡∏ô‡∏±‡∏ö‡∏ô‡∏±‡∏î‡∏ú‡πà‡∏≤‡∏ï‡∏±‡∏î‡∏ñ‡πâ‡∏≤‡∏°‡∏µ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà
      if (surgeryDate && surgeryDate.trim() !== "") {
        surgeryCounts[extractedId] = (surgeryCounts[extractedId] || 0) + 1;
      }
      return;
    }
    // ‡∏ô‡∏±‡∏ö consult ‡∏ñ‡πâ‡∏≤‡∏°‡∏µ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà
    if (consultDate && consultDate.trim() !== "") {
      consultCounts[agentId] = (consultCounts[agentId] || 0) + 1;
    }
    // ‡∏ô‡∏±‡∏ö‡∏ô‡∏±‡∏î‡∏ú‡πà‡∏≤‡∏ï‡∏±‡∏î‡∏ñ‡πâ‡∏≤‡∏°‡∏µ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà
    if (surgeryDate && surgeryDate.trim() !== "") {
      surgeryCounts[agentId] = (surgeryCounts[agentId] || 0) + 1;
    }
  });
  return {
    consultCounts,
    surgeryCounts,
  };
}